						<div class="stars_wrap">
						<p class="stars"  v-on:click="isActive = true" v-bind:class="{selected:isActive}">
							<span>
								<a class="star-1" href="#" @click="activate(1)" :class="{ active : active_el == 1 }">1</a>
								<a class="star-2" href="#" @click="activate(2)" :class="{ active : active_el == 2 }">2</a>
								<a class="star-3" href="#" @click="activate(3)" :class="{ active : active_el == 3 }">3</a>
								<a class="star-4" href="#" @click="activate(4)" :class="{ active : active_el == 4 }">4</a>
								<a class="star-5" href="#" @click="activate(5)" :class="{ active : active_el == 5 }">5</a>
							</span>
							<p class="stars__rate">Tap a Star to Rate</p>
