<script>
	import { eventBus } from '../main';

	export default {
		data: function() {
			return {

				active_el:0,
				isActive: false
			}
			
		},
		props: { 
			userDest: String,
			showMe: String
		},
		methods: {   
			resetName() {
				this.myName = "qwert-leave";
				this.$emit('nameWasReset', this.myName);
			},
			myFilter: function(){
	            this.isActive = !this.isActive;
	          // some code to filter users
	        },
	        activate:function(el){
		        this.active_el = el;
		    }

		}
	} 
	
</script>

