	.star-rating {
	    font-size: 20px;
	    line-height: 1;
	    height: 1em;
	    overflow: hidden;
	    position: relative;
	    width: 5.3em;
	    font-family: star;
	    font-weight: 400;
	    &:before {
		    content: "";
		    opacity: .25;
		    float: left;
		    top: 0;
		    left: 0;
		    position: absolute;
		    color: blue;
		}
		span {
		    overflow: hidden;
		    float: left;
		    top: 0;
		    left: 0;
		    position: absolute;
		    padding-top: 1.5em;
